<template>
    <div class="operationLog-page">
        <separator></separator>
        <div class="operationLog-content">
          <div class="top-box">
            <i class="blue"></i>
            <div class="title">
              <span>{{$t("operation.OperationLog")}}</span>
            </div>
            <div class="search-box">
              <div class="search-box-left">
              <span>{{$t("operation.administratorAccount")}}：</span>
              <el-input
                  class="mini-input administratorAccount-input"
                  :placeholder="$t('operation.administratorAccount')"
                  v-model="searchFrom.adminUsername"
                  style="width:150px">
              </el-input>
              <span style="margin-left:0.2rem;">{{$t("operation.mainFunction")}}：</span>
              <el-input
                  class="mini-input mainFunction-input"
                  :placeholder="$t('operation.mainFunction')"
                  v-model="searchFrom.op"
                  style="width:150px">
              </el-input>
              </div>
              <div class="search-box-right">
                <span class="time-label">{{$t("operation.time")}}：</span>
                <el-date-picker
                    class="picker time-picker"
                    v-model="searchFrom.dateSection"
                    type="daterange"
                    :range-separator="$t('operation.to')"
                    :start-placeholder="$t('operation.startDate')"
                    :end-placeholder="$t('operation.endDate')">
                </el-date-picker>
              <el-button type="primary" class='search-button' @click="getData()" >{{$t("operation.search")}}</el-button>
              </div>
            </div>
          </div>
          <div class="table-box" ref="tableBox">
              <el-table
                  v-loading="tableBoxLoading"
                  ref="table"
                  :data="tableData"
                  height="611"
                  :header-cell-style="{background:'rgba(245,248,252,1)'}"
                  >
                  <!--<el-table-column-->
                    <!--fixed-->
                    <!--type="selection"-->
                    <!--width="55"-->
                  <!--&gt;</el-table-column>-->
                  <el-table-column
                  v-for="(tab,index) in tableDataLabel"
                  :key="index"
                  :prop="tab.property"
                  :label="$i18n.locale=='en'?tab.enName:tab.name"
                  :min-width="tab.width"
                  align="center"
                  >
                  </el-table-column>
              </el-table>
              <div class="pagination-box">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-sizes="[5,10,20]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="total">
                </el-pagination>
              </div>
          </div>

        </div>
        </div>
</template>
<script>
import separator from "../../../../public/separator.vue";
import TABLE_TYEPS from "../../../../../config/tableType.js";
import APICONFIG from "../../../../../config/api.js";
export default {
  name: "operation_log",
  components: {
    separator
  },
  data() {
    return {
      searchFrom: {
        dateSection: "", //时间
        adminUsername: "", //管理员ID
        op: "" //具体操作
      },
      tableDataLabel: "",
      tableBoxLoading: false,
      netAPI: "",
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      total: 0
    };
  },
  created() {
    this.tableDataLabel = TABLE_TYEPS.get("operation_log");
    this.netAPI = APICONFIG().get("operation_log");
    this.getData();
  },
  methods: {
    getData(page = { pageSize: this.pageSize, pageNo: 1 }) {
      this.tableBoxLoading = true;
      let api;
      for (let i in this.searchFrom) {
        if (this.searchFrom[i]) {
           // notNULL = true;
          if (i == "dateSection") {
            page["startDate"] = this.formatDate(this.searchFrom.dateSection[0]);
            page["endDate"] = this.formatDate(this.searchFrom.dateSection[1]);
          } else {
            page[i] = this.searchFrom[i];
          }
        }
      }
      api = this.netAPI.find;
      this.currentPage = page.pageNo;
      this.$http.get(api, { params: page }).then(
        res => {
          if (res.body.status == 200) {
            this.tableData = res.body.data;
            this.total = res.body.total;
            this.tableBoxLoading = false;
            for (let item of this.tableData) {
              item.timestamp = this.parseDate(item.timestamp);
            }
          }
        }
      );
    },
    parseDate(str) {
      let d = new Date(str);
      let youWant =
        d.getFullYear() +
        "-" +
        (d.getMonth() + 1) +
        "-" +
        d.getDate() +
        " " +
        d.getHours() +
        ":" +
        d.getMinutes() +
        ":" +
        d.getSeconds();
      return youWant;
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      console.log(pageSize)
      this.getData({ pageSize: this.pageSize, pageNo: this.currentPage });
    },
    handleCurrentChange(Current) {
      this.currentPage = Current;
      console.log(Current)
      this.getData({ pageSize: this.pageSize, pageNo: this.currentPage });
    },
    formatDate(date) {
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "-" + m + "-" + d;
    }
  }
};
</script>

<style lang="scss">
.operationLog-page{
  .operationLog-content{
    background:rgba(255,255,255,1);
    border:1px solid rgba(233,233,233,1);
    box-shadow:#D1D1D1 0 2px 4px 0;
    border-radius:5px;
    margin-top: 10px;
    .top-box{
      position: relative;
      box-sizing: border-box;
      padding: 10px 30px;
      .blue{
          width:12px;
          height:20px;
          background:rgba(67,131,220,1);
          border-radius:0px 3px 3px 0px;
          position: absolute;
          left: 0;
          top: 19px;
        }
      .title{
          height: 0.77rem;
          display: flex;
          align-items: center;
          font-size: 20px;
          font-weight: bolder;
      }
      .search-box{
        margin: 0.2rem 0;
        height: 40px;
        .search-button{
          margin-left: 10px; 
        }
        .search-box-left{
          float: left;
        }
        .search-box-right{
          float: left;
          @media screen and(max-width:1470px){
            margin-top: 10px; 
            margin-bottom: 10px; 
          }
        }
        
        .mainFunction-input{
            margin-right: 10px; 
          }
          
        // // @media screen and(max-width:1470px){
        // //   .search-button{
        // //     padding: 12px 10px !important;
        // //   }
        // // }
        // @media screen and(max-width:1380px){
        // //   .mainFunction-input{
        // //     margin-right: 370px !important; 
        // //   }
        //   .time-picker{
        //     margin-top: 10px; 
        //   }
        // }
      }
    }
    .table-box {
      box-sizing: border-box;
      padding: 0 30px;
    .pagination-box{
          height: 50px;
          display: flex;
          justify-content: center;
          align-items: center;
        }
    }
  }
}
</style>





